//STYLES
import '../styles/index.scss';

//FONTS
require('typeface-oswald');
require('typeface-roboto');
require('@openfonts/oswald_cyrillic');

//PLUGINS
const $ = require("jquery");
import 'slick-carousel';


$(document).ready(function () {

    $('.carousel').slick({
        arrows: false,
        dots: true,
        autoplay: true
    });
    $('.brands-carousel__wrap').slick({
        arrows: true,
        dots: false,
        autoplay: false,
        slidesToShow: 3,
        slidesToScroll: 1,
        responsive: [
            {
                breakpoint: 590,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1,
                }
            },
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                }
            }
        ]
    });

    $('.cards-carousel .cards-wrap').slick({
        arrows: true,
        dots: false,
        autoplay: false,
        slidesToShow: 5,
        slidesToScroll: 1,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 4
                }
            },
            {
                breakpoint: 992,
                settings: {
                    slidesToShow: 3
                }
            },
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 2
                }
            },
            {
                breakpoint: 430,
                settings: {
                    slidesToShow: 1
                }
            },
        ]
    });
    $('.cards-carousel.recent-articles__wrap').slick({
        arrows: true,
        dots: false,
        autoplay: false,
        slidesToShow: 1,
        slidesToScroll: 1,
    });
    $('.card-item-info__gallery .top-img').slick({
        arrows: false,
        dots: true,
        autoplay: false,
        slidesToShow: 1,
        slidesToScroll: 1,
        asNavFor: '.card-item-info__gallery .gallery'
    });
    $('.card-item-info__gallery .gallery').slick({
        arrows: false,
        dots: false,
        autoplay: false,
        slidesToShow: 4,
        slidesToScroll: 1,
        asNavFor: '.card-item-info__gallery .top-img',
        focusOnSelect: true
    });


    $(function () {
        (function quantityProducts() {
            const $quantityArrowMinus = $(".quantity-arrow-minus");
            const $quantityArrowPlus = $(".quantity-arrow-plus");
            const $quantityNum = $(".quantity-num");

            $quantityArrowMinus.click(quantityMinus);
            $quantityArrowPlus.click(quantityPlus);

            function quantityMinus() {
                if ($quantityNum.val() > 1) {
                    $quantityNum.val(+$quantityNum.val() - 1);
                }
            }

            function quantityPlus() {
                $quantityNum.val(+$quantityNum.val() + 1);
            }
        })();

    });

    $('.tab-switch__item').on('click', function (e) {
        e.preventDefault();
        $('.tab-switch__item').removeClass('active');
        $(this).addClass('active');

        $('.tab-content__item').removeClass('active');
        $(`.tab-content__item[data-tab='${$(this).data("tab")}']`).addClass('active');
    });


    $('.shop-items__buy').on('click', function (e) {
        e.preventDefault();
        $('.modal-basket').addClass('active');
    });

    $('.modal__close').on('click', function () {
        $(this).parent().parent().removeClass('active');
    });


    $('.modal').on('click', function (e) {
        if (e.target !== this)
            return;
        $(this).removeClass('active');
    });


    $('.mobile-menu__overlay').on('click', function (e) {
        if (e.target !== this)
            return;
        $('.mobile-menu').removeClass('active');
    });

    $('.mobile-menu-trigger, .mobile-menu__close').on('click', function () {
        $('.mobile-menu').toggleClass('active');
    });

    $('.inner-menu-trigger').on('click', function () {
        $(this).next().slideToggle();
    });
});
